import { Component } from '@angular/core';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { CONFIG } from './config';
import { LoginComponent } from './login.component';
import { Router } from '@angular/router';
import { Services } from './services';
@Component({
  selector: '',
  templateUrl: './registeruser.component.html'

})
export class RegisterUserComponent {
  CONFIG: any;
  public email: String;
  public firstname: String;
  public password: String;
  public lastname: String;
  public confirmpassword: String;

  constructor(
    private _router: Router,
    private data: Services
  ) {
    this.CONFIG = CONFIG();
  }
  register() {
    let context = this;

    if (this.password != this.confirmpassword) {
      alert("Password does not match");
      return false;
    }

    let validateEmail = "" + this.email;
    var regex = /^(([^<>()\[\]\\.,;:+%*\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    if (!regex.test(validateEmail)) {
      alert("Not a valid email format");
      return false;
    }
    var data = JSON.stringify({
      "email": "" + this.email,
      "firstName": "" + this.firstname,
      "lastName": "" + this.lastname,
      "password": "" + this.password
    });
    console.log(data);
    var xhr = new XMLHttpRequest();
    xhr.addEventListener("readystatechange", function () {
      if (this.readyState === 4) {
        //console.log(this.responseText);
        let data = JSON.parse(this.responseText);
        if (data.status == "success") {
          context.onSuccess();
        }
        else {
          alert(data.error);
        }

      }
    });

    xhr.open("POST", this.CONFIG.registerURL);
    xhr.setRequestHeader("content-type", "application/json");
    xhr.setRequestHeader("cache-control", "no-cache");


    xhr.send(data);

  }
  catalog() {
    this.data.loginSuccess = true;
    this._router.navigateByUrl("");
  }
  onSuccess() {
    alert("User registered successfully");
    this.data.loginSuccess = true;
    var data = null;
    let context = this;
    var xhr = new XMLHttpRequest();
    xhr.addEventListener("readystatechange", function () {
      if (this.readyState === 4) {
        console.log(this.responseText);
        let temp = JSON.parse(this.responseText);
        if (temp.status == "failure") {
          alert(temp.error);
        }
        else {
          context.catalog();
        }
      }
    });

    xhr.open("GET", this.CONFIG.loginURL);
    xhr.setRequestHeader("authorization", "Basic " + btoa(this.email + ":" + this.password));
    xhr.setRequestHeader("cache-control", "no-cache");
    xhr.send(data);

    this.email = "";
    this.firstname = "";
    this.password = "";
    this.confirmpassword = "";
    this.lastname = "";
  }
}
