import { Component } from '@angular/core';
import { Http, URLSearchParams } from '@angular/http';
import { CONFIG } from './config';
declare var $: any;
@Component({
  selector: '',
  templateUrl: './catalog.component.html'

})
export class CatalogComponent {
  CONFIG: any;
  public productList = [];
  constructor(
    private http: Http,
  ) {
    this.CONFIG = CONFIG();
    this.getProductslist();
  }
  getProductslist() {
    this.http.get(this.CONFIG.getProductslistURL).subscribe((response: any) => {
      let data = JSON.parse(response._body);
      this.productList = data.data.productList;
      console.log(data);

    }, (error) => {
      console.log(error);
    });

  }

  showmodel(p) {
    console.log($);
    $("#soccer-socks-" + p).addClass("in");
    $("#soccer-socks-" + p).css("display", "block");
    $("body").addClass("modal-open");
    $("#modelbackdrop").addClass("modal-backdrop fade in")
  }

  hidemodel(p) {
    console.log(p);
    $("#soccer-socks-" + p).removeClass("in");
    $("#soccer-socks-" + p).css("display", "none");
    $("body").removeClass("modal-open");
    $("#modelbackdrop").removeClass("modal-backdrop fade in");
  }
}
